.intro {
  margin: auto;
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;
  width: 400px;
  height: 300px;
  text-align: left;
  font-size: 50px;
  z-index: 100;
  color: #ffffff;
 
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);


  &_line{
    transition: width 2s;
    width: 0px;
    height: 5px;
    background: #83cae8;
    border-radius: 2.5px;
  }
}

.addWidth {
  //width: 40vw;
  width: 300px;
}

