.toolbar {
  position: absolute;
  height: 100%;
  width: 90px ;
  background: #3D3D3D;
  opacity: .7;
  border-right: 3px solid #D2CFCF;
  transition: width 2s, opacity 3s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &_full {
    width: 100%;
    opacity: 1;

    .link,.nav_lang {
      opacity: 0;
    }

  }

  .nav_lang {
    float: left;
    display: flex;
    justify-content: center;
    width: 90px;
    height: 40px;
    margin-top: 10px;
    transition: opacity 6s;
  }
  .lang_active {
    font-weight: bold;
  }

  .lang_item {
    cursor: pointer;
  }

  .split {
    padding: 0 5px;
  }

  .nav {
    margin-top: 10vh;
  }

  .nav_sub {
    margin-bottom: 40px;
  }

  .link {
    padding: 20px 0;
    width: 90px;
    min-height: 20px;
    transition: background-color 1s, color 1s, font-weight 1s, opacity 6s;
    &:hover {
      background: #83CAE8;
      color: #61dafb;
      font-weight: bold;
    }
  }



}
#link0 {
  -webkit-animation: link0 7s linear infinite;
  animation: link0 7s linear infinite;
}
@-webkit-keyframes link0 {
  0% { background-color: rgba(255, 255, 255, 0);}
  10% { background-color: #83CAE8; }
  20% { background-color: rgba(255, 255, 255, 0); }
}
@keyframes link0 {

  0% { background-color: rgba(255, 255, 255, 0);}
  10% { background-color: #83CAE8; }
  20% { background-color: rgba(255, 255, 255, 0); }
}

